import data from "../data/portfolio";

let initialState = {
  data,
  portfolios: data
};

const items = (state = initialState, action) => {
  switch (action.type) {
    case "SET_HOVER":
      return {
        ...state,
        portfolios: state.portfolios.map(portfolio =>
          portfolio.id === action.payload.id
            ? { ...portfolio, active: action.payload.active }
            : portfolio
        )
      };
    case "SEARCHING":
      let query = action.payload.event.target.value.toLowerCase(),
        displayed = state.data.filter(el => {
          let searchValue = el.name.toLowerCase();
          return searchValue.indexOf(query) !== -1;
        });
      return {
        ...state,
        portfolios: displayed
      };
    default:
      return state;
  }
};

export default items;
