import React from "react";
import { Col, Form } from "react-bootstrap";

const Search = ({ items }) => {
  return (
    <Col md={4} className="mt-4">
      <Form.Group controlId="formSearch">
        <Form.Control
          type="text"
          placeholder="Search"
          name="search"
          onChange={(event) => items(event)}
        />
      </Form.Group>
    </Col>
  );
};

export default Search;
